:root {
  
  --button-color: #E0A39A;
  --button-color-hover: #F2C0BD;
  --text-color-grey: #8A8A89;
  --text-color: #121417;
  --text-color-active: rgb(52, 112, 255);
  --back-grey: #F8F8F8;
}

* {
  box-sizing: border-box;
  transition: color 350ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-bottom 250ms cubic-bezier(0.4, 0, 0.2, 1),
    stroke 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-radius 350ms cubic-bezier(0.4, 0, 0.2, 1),
    background-image 350ms cubic-bezier(0.4, 0, 0.2, 1);

  &::before {
    transition: background-image 350ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

/* Reset styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
textarea,
button,
label {
  display: block;
  padding: 0;
  margin: 0;
  font-weight: inherit;
  letter-spacing: 0.04em;
  font-size: 12px;
  line-height: 1.5;
}

body {
  margin: 0 auto;
  padding: 20px 64px;
  max-width: 1440px;
  font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @media(max-width: 700px) {
      padding: 20px 20px;
    }
}

.popup-content {
  width: 100%;}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
